#project-container {
  overflow: hidden;

  padding: 40px;
  width: calc(100% - 80px);
}

@media (max-width: 1000px) {
  #project-container {
    padding: 30px;
    width: calc(100% - 60px);
  }
}

@media (max-width: 480px) {
  #project-container {
    padding: 20px;
    width: calc(100% - 40px);
  }
}



