.header {
  font-size: 40px;
  text-align: center;
}

.page-container {
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0;

  overflow-x: hidden;
  overflow-y: auto;
}


@media (max-width: 599px) {
  .page-container {
    top: 48px;
  }
}

@media (max-width: 480px) {
  .page-container {
    top: 104px;
  }
}

@media (max-width: 480px) and (orientation: landscape) {
  .page-container {
    top: 96px;
  }
}



